.example {
  color: #424242;
  max-width: 960px;
  background-color: #fff;
  padding: 16px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.example code {
  color: #fff;
  background-color: #009688;
  border-radius: 2px;
  padding: 2px 6px;
  font-family: monospace;
  font-size: 16px;
  font-weight: 700;
}

.example .background {
  height: 240px;
  background-image: url("demo.e3ba131c.jpg");
  background-position: center;
  background-size: cover;
}

* {
  margin: 0;
  padding: 0;
}

body {
  color: #000;
  background-color: #fff;
  font-family: DM Sans;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  padding: 0 110px;
}

.list {
  list-style-type: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.wide-btn {
  text-align: center;
  border: none;
  border-radius: 20px;
  align-items: center;
  display: flex;
}

.wide-btn--red {
  color: #fff;
  background-color: #d41443;
}

.wide-btn--white {
  color: #d41443;
  background-color: #fff;
}

.round-btn {
  content: "";
  width: 40px;
  height: 40px;
  background: url("arrow-right.6b956aa5.png");
  border: none;
}

.contacts {
  background-color: #fff5f6;
}

.contacts__title {
  display: none;
}

.contacts__card {
  max-width: 220px;
  background-color: #fff;
  border-radius: 24px;
  margin: 0 auto;
  padding: 43px 21px 43px 20px;
  box-shadow: 0 8px 30px #d414431a;
}

.contacts__name {
  width: 80px;
  height: 16px;
  background-color: #f0d1a5;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin-bottom: 25px;
  display: flex;
}

.contacts__name--foodtruck {
  width: 130px;
  background-color: #c2e297;
}

